import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import {selectFriends} from '../../store/reducers';
import {frendsActions} from '../../store/actions';

import {Panel,Group} from '@vkontakte/vkui';
import HeaderPanel from '../../components/HeaderPanel';

import { LineChart, Line, PieChart, Pie, Sector, Cell } from 'recharts';

const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }) => {
	const RADIAN = Math.PI / 180;
	const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
	const x  = cx + radius * Math.cos(-midAngle * RADIAN);
	const y = cy  + radius * Math.sin(-midAngle * RADIAN);

	return (
		<text x={x} y={y} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central">
			{`${(percent * 100).toFixed(0)}%`}
		</text>
	);
};

class City extends Component  {
	constructor(props) {
		super(props);
	}

	componentWillMount() {
		const {dispatch} = this.props;
		const {fetchedAllFriends} = frendsActions;
		dispatch(fetchedAllFriends());
	}

	render () {
		const data = [
			{key: '0', name: 'Group A', value: 400}, {key: '1', name: 'Group B', value: 300},
			{key: '2', name: 'Group C', value: 300}, {key: '3', name: 'Group D', value: 200}];
		const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];
		const {id, go, users} = this.props;
		return (
			<Panel id={id}>
				<HeaderPanel title='Города' handelClick={go} dataTo='home' />
				<Group>
					<PieChart width={400} height={400} onMouseEnter={this.onPieEnter}>
						<Pie
							dataKey='value'
							data={data} 
							cx="50%" 
							cy="30%" 
							labelLine={false}
							label={renderCustomizedLabel}
							outerRadius={80} 
							fill="#8884d8"
						>
							{data.map((entry, index) => <Cell key={index} fill={COLORS[index % COLORS.length]}/>)}
						</Pie>
					</PieChart>
				</Group>
			</Panel>
		);
	}
}

City.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

const mapStateToProps = (state) => ({
	users: selectFriends.getFriends(state)
});
export default connect(mapStateToProps, null)(City);